.wrapper {
  position: relative;
  top: 0;
  width: 100%;
  height: 100%;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--backgroundColor);
  z-index: 100;
  transition: background-color 0.3s;
}
.navBlock {
  position: relative;
  display: flex;
  grid-gap: 30px;
  margin: 40px 0 0;
}
.navBlock_a {
  position: relative;
  transition: 0.3s;
  font-size: 120%;
  font-weight: 500;
  white-space: nowrap;
  /*letter-spacing: 0.5px;*/
}
.navBlock_a::before {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 50%;
  transform: translateX(-50%);
  height: 0.5px;
  width: 0;
  background: #c4a370;
  border-radius: 50%;
  transition: 0.3s;
}
.navBlock_a:hover::before{
  width: 100%;
}
.long {
  position: relative;
  width: 100%;
  height: 1px;
  background: #c4a370;
  border-radius: 50%;
  opacity: 0.5;
  margin-top: 20px;
}

@media (max-width: 800px) {
  .wrapper {
    position: sticky;
    top: -1px;
    padding-top: 10px;
  }
  .long {
    margin-top: 10px;
  }
  .navBlock {
    display: none;
  }
}