.wrapper {
  display: flex;
  grid-gap: 20px;
  flex-direction: column;
  min-width: 300px;
}
.title {
  position: relative;
  text-align: center;
  font-weight: 700;
  font-size: 110%;
}
.wrapper a:hover{
  text-decoration: none;
}
.backBtn {
  position: absolute;
  top: 13px;
  left: 10px;
  width: 30px;
  height: 20px;
  cursor: pointer;
}