.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  transition: opacity 0.3s;
  opacity: 0;
}
.wrapperBlock {
  opacity: 1;
}
.wrapperBlock .block {
  opacity: 1;
}
.block {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  top: 10vh;
  width: 60%;
  opacity: 0;
  background: var(--backgroundColor);
  border-radius: 5px;
  padding: 30px;
  transition: 0.3s;
  overflow: hidden;
  padding-bottom: 50px;
  max-height: 80vh;
  height: min-content;
  display: flex;
  flex-direction: column;
}
.fon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: #00000080;
}
.formBlock {
  display: flex;
  grid-gap: 20px;
  min-height: 50px;
  height: 50px;
}
.form {
  position: relative;
  width: 100%;
  height: 100%;
}
.inputIcon {
  position: absolute;
  left: 15px;
  top: 50%;
  width: 15px;
  height: 15px;
  transform: translateY(-50%)  scale(1);
}
.input {
  position: relative;
  width: 100%;
  height: 100%;
  background: none;
  outline: none;
  border: 1px solid var(--color);
  padding: 0px 20px;
  padding-left: 40px;
  font-weight: 500;
}
.productsWrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
  display: flex;
  margin-top: 30px;
}
.productsBlock {
  position: relative;
  width: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
}
.shadow {
  position: absolute;
  top: -10px;
  left: 0;
  width: calc(100% - 50px);
  height: 0;
  box-shadow: -1px 1px 20px 40px var(--backgroundColor);
}
.products {
  position: relative;
  width: 100%;
  height: 100%;
  grid-gap: 10px;
  display: flex;
  flex-direction: column;
}
.info {
  position: relative;
  margin: 10px 0;
  margin-left: 5px;
}
.searchNone {
  text-align: center;
  font-weight: 700;
  font-size: 100%;
  opacity: 0.5;
  margin-top: 35px;
  margin-bottom: 20px;
}
.cross {
  position: relative;
  align-self: center;
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: 0.3s;
}
.cross:hover {
  opacity: 0.5;
}
@media (max-width: 800px) {
  .block {
    top: 10px;
    padding: 10px;
    width: 95%;
    height: min-content;
    padding-bottom: 30px;
    max-height: calc(100vh - 20px);
  }
  .productsWrapper {
    margin-top: 10px;
  }
  .wrapperBlock .block {
    opacity: 1;
  }
  .form {
    grid-gap: 20px;
  }
  .cross {
    right: 5px;
  }
}