.wrapper {
  position: fixed;
  top: 0;
  right: 0;
  width: 75%;
  height: 100vh;
  background: var(--backgroundColor);
  transition: 0.3s ease;
  transform: translateX(0%);
  z-index: 101;
  padding: 15px 30px;
  padding-bottom: 70px;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}
.fon {
  position: fixed;
  top: 0px;
  right: 0px;
  background: rgba(0, 0, 0, 0.36);
  width: 100vw;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease;
}
.fonOn {
  opacity: 1;
  visibility: visible;
}
.wrapperOff {
  transform: translateX(100%);
}
.btnBack {
  position: relative;
  outline: none;
  border: none;
  background: none;
  height: 30px;
  align-self: start;
  margin-left: -20px;
}
.btnBack img{
  position: relative;
  height: 100%;
}
.ThemeInputWrapperMobil {
  margin-bottom: 0;
  margin-top: auto;
  display: flex;
  justify-content: end;
}

.menuBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  margin-top: 50px;
}
.wrapperAuthMenuMobil {
  display: flex;
  align-items: center;
  grid-gap: 10px;
}
.wrapperAuthMenuMobil p{
  white-space: nowrap;
  font-weight: 600;
  font-size: 140%;
}
.wrapperAuthMenuMobil img{
  transform: translateY(2px);
  height: 17px;
}
.menuBlock p {
  position: relative;
  font-weight: 500;
  font-size: 100%;
  text-transform: uppercase;
}
.menuBlockShopItem {
  position: relative;
  display: flex;
  grid-gap: 10px;
  align-items: center;
  transition: 0.1s;
}
.menuBlockShopItem:active {
  opacity: 0.5;
}
.menuBlockShopItemOffIcon {
  position: relative;
  height: 10px;
  width: 5px;
  transform: translateY(1px) rotate(0deg);
  transition: 0.3s;
}
.menuBlockShopItemOnIcon {
  transform: translateY(1px) rotate(90deg);
}
.name {
  position: relative;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 140%;
}
.orders {
  transition: 0.3s;
  position: relative;
  margin-top: 30px;
  display: flex;
  grid-gap: 10px;
}
.orders:active {
  opacity: 0.5;
}
.orders p{
  font-size: 130%;
  font-weight: 700;
  text-transform: uppercase;
}
.orders img{
  align-self: center;
  transform: translateY(1px);
  height: 20px;
}
.wrapperAuthButtonsMobil {
  padding: 15px;
  font-size: 110%;
  font-weight: 500;
  border-radius: 5px;
  box-sizing: content-box;
  color: var(--color);
  text-align: center;
  border: 1px solid var(--color);
  transition: 0.3s;
}
.wrapperAuthButtonsMobil:hover {
  background: var(--color);
  color: var(--backgroundColor);
}
.openBtn {
  position: relative;
  margin-left: 0;
  margin-right: auto;
  height: 40px;
  width: 40px;
}
.openBtn img{
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%) scale(1);
  animation: 1s infinite alternate openScreen;
  height: 120%;
  width: 120%;
}
@keyframes openScreen {
  0%{
    transform: translateY(-50%) scale(1);
  }
  50%{
    transform: translateY(-50%) scale(0.8);
  }
  100%{
    transform: translateY(-50%) scale(1);
  }
}