.container {
  width: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.wrapperTypes {
  width: auto;
  flex-wrap: wrap;
  border-radius: 5px;
  /*padding: 10px;*/
  /*border: 1px solid var(--color);*/
  position: relative;
  display: flex;
  gap: 10px;
}
.mainBtn {
  padding: 7px 10px;
  border: 1px solid var(--color);
  text-align: start;
}
.mainBtnActive {
  background: red;
  background: var(--color);
  color: var(--backgroundColor);
}