.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.block {
  position: relative;
  display: flex;
  grid-gap: 60px;
  margin-bottom: 10px;
  margin-top: 20px;
}
.title {
  position: relative;
  text-align: center;
  font-weight: 700;
  font-size: 110%;
}
.label p{
  margin-bottom: 10px;
  font-weight: 500;
}
.checkoutOneInfo {
  position: relative;
  text-align: end;
  margin-bottom: 20px;
}
@media (max-width: 800px) {
  .block {
    position: relative;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    margin: 20px 0 ;
  }
}