.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  margin: 15px 0;
  transition: 0.4s;
}
.wrapperShow {
  transform: translateX(-100%);
  opacity: 0;
}
.block_left {
  display: flex;
  align-items: center;
  grid-gap: 30px;
  width: 100%;
  min-width: 100px;
  transition: opacity 0.3s;
}
.block_left:hover {
  opacity: 0.6;
}
.block_left p{
  font-weight: 500;
}
.photo {
  border-radius: 5px;
  position: relative;
  overflow: hidden;
  min-width: 100px;
  min-height: 100px;
}
.photo img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.block_center {
  white-space: nowrap;
  align-self: center;
  width: min-content;
  justify-self: end;
}
.block_right {
  width: min-content;
  align-self: center;
  justify-self: end;
  height: 50px;
  display: flex;
  grid-gap: 50px;
}
.deleteItem {
  position: relative;
  align-self: center;
  min-width: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  transition: opacity 0.3s;
  margin-left: 40px;
}
.deleteItem:hover {
  opacity: 0.5;
}
.deleteItem img{
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: 100%;
  transform: translate(-50%, -50%);
}
@media (max-width: 700px) {
  .wrapper {
    margin: 20px 0;
    flex-direction: column;
  }
  .deleteItem {
    margin-left: 0px;
  }
  .block_right {
    position: absolute;
    bottom: 5px;
    right: 5px;
    grid-gap: 20px;
    height: 40px;
  }
  .block_left {
    grid-gap: 20px;
  }
  .block_left p{
    margin-top: 10px;
    margin-bottom: auto;
    font-size: 90%;
  }
}