.wrapper {
  position: sticky;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 50;
  margin: 0;
  padding: 20px 0;
}
.wrapper::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150%;
  height: 100%;
  background: var(--backgroundColor);
  transition: background-color 0.3s;
}
.block {
  width: 100%;
  max-width: 800px;
  position: relative;
  display: flex;
}
.block a {
}
@media (max-width: 800px) {
  .wrapper {
    display: none;
  }
}

