.wrapper {
  position: relative;
  width: max-content;
  height: max-content;
  margin: 0 auto;
}
.wrapper:first-child {
  margin-right: auto;
  margin-left: 0;
}
.wrapper:last-child {
  margin-right: 0;
  margin-left: auto;
}
.main {
  position: relative;
  text-align: center;
  cursor: pointer;
  font-size: 90%;
  font-weight: 400;
}
.childBlock {
  position: absolute;
  left: -10px;
  top: 100%;
  display: flex;
  flex-direction: column;
  padding: 10px;
  grid-gap: 10px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background: var(--backgroundColor);
  width: max-content;
  z-index: 101;
  min-width: calc(100% + 20px);
  padding-top: 20px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  transform: translateY(50px);
}
.childBlockShow {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.childItem {
  font-size: 75%;
  white-space: nowrap;
  text-align: start;
  position: relative;
  cursor: pointer;
  text-transform: capitalize;
  transition: 0.3s;
}
.childItem:hover {
  opacity: 0.7;
}
