.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  transition: opacity 0.3s;
}
.wrapperOff {
  opacity: 0;
}
.block {
  position: fixed;
  left: 50%;
  top: 50%;
  max-width: 500px;
  width: max-content;
  background: var(--backgroundColor);
  transform: translate(-50%, -50%);
  z-index: 100;
  border-radius: 5px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  transition: 0.3s;
}
.blockOff {
   transform: translate(-50%, 200%);
 }
.crossBtn {
  position: absolute;
  width: 20px;
  right: 10px;
  top: 10px;
  cursor: pointer;
  transition: opacity 0.3s;
}
.crossBtn:hover {
  opacity: 0.5;
}
.fon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: #00000080;
}
.block input{
  background: none;
  outline: none;
  border: 1px solid var(--color);
  padding: 10px;
}
.block button{
  padding: 10px;
}
@media (max-width: 800px) {
  .block {
    width: 95%;
    padding: 30px 20px;
  }

}