.basketActive {
  height: 25px;
}
.basketActiveOn {
  transform-origin: center;
  animation-name: basketActive;
  animation-duration: 0.5s;
}
.quantity {
  position: absolute;
  right: -7px;
  bottom: -7px;
  background: var(--backgroundColor);
  border-radius: 50px;
  font-size: 60%;
  font-weight: 700;
  min-width: 20px;
  min-height: 20px;
  width: max-content;
  height: max-content;
  border: 1px solid #c4a370;
  display: flex;
  padding: 0 3px;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s;
}
@keyframes basketActive {
  0%{transform: scale(1) rotateZ(30deg)}
  25%{transform: scale(1) rotateZ(-30deg)}
  50%{transform: scale(1) rotateZ(30deg)}
  75%{transform: scale(1) rotateZ(-30deg)}
  100%{transform: scale(1) rotateZ(0deg)}
}