.wrapper {
  position: relative;
  margin: 50px 0;
  width: 100%;
}
.block {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.name {}
.emptyBasket {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}
.emptyBasket p{
  font-weight: 600;
  opacity: 0.3;
  text-transform: uppercase;
  font-size: 130%;
}
.emptyBasket a{
  margin-top: 20px;
  margin-left: 5px;
  text-decoration: underline;
}
.emptyBasket a img{
  position: relative;
  width: 10px;
  margin-left: 5px;
}
.emptyBasket a:hover{
  text-decoration: none;
}
.basketForm {
  position: relative;
  margin-right: 0;
  margin-left: auto;
  align-items: end;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  width: max-content;
  grid-gap: 10px;
  font-size: 100%;
}
.basketForm button {
  padding: 15px;
  font-size: 80%;
}
@media (max-width: 700px) {
  .wrapper {
    margin: 20px 0;
  }
}
