.wrapper {
  position: relative;
  width: 100%;
  background: #423b34;
  padding: 15px;
  text-align: center;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
}
.wrapper p {
}
.content {
  font-weight: 500;
  font-size: 110%;
  color: white;
  font-family: Akrobat, sans-serif;
}
.contentL {
  font-weight: 500;
  font-size: 70%;
  color: white;
  font-family: Akrobat, sans-serif;
}
.to img {
  height: 10px;
  transform: rotate(180deg) translateY(-1px);
}
.to {
  display: flex;
  gap: 10px;
  align-items: center;
  font-weight: 600;
  width: max-content;
  margin: 0 auto;
  font-size: 80%;
  transition: 0.3s;
  animation: to 2s infinite;
}
@keyframes to {
  0% {transform: translateX(10px); opacity: 0.8}
  50% {transform: translateX(0px); opacity: 1}
  100% {transform: translateX(10px); opacity: 0.8}
}
.to:hover {
  opacity: 0.7;
}
.btnClose {
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  height: 20px;
  cursor: pointer;
}
.btnClose img{
  position: relative;
  height: 100%;
}
@media (max-width: 800px) {
  .wrapper {
    padding: 15px 45px;
    padding-left: 15px;
  }
  .content {
    font-size: 90%;
  }
  .btnClose {
    height: 15px;
    right: 15px;
  }
  .to {
    font-weight: 600;
    width: max-content;
    margin: 0 auto;
    font-size: 70%;
    transition: 0.3s;
  }
}