.switch {
  width: 60px;
  height: 33px;
  background: none;
  border: 2px solid var(--color);
  display: flex;
  justify-content: flex-start;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
}

.switch[data-isOn="true"] {
  justify-content: flex-end;
}

.handle {
  width: 20px;
  height: 20px;
  align-self: center;
  background-color: var(--color);
  border-radius: 3px;
}
@media (max-width: 800px) {
  .switch {
    width: 80px;
    height: 40px;
  }
  .handle {
    width: 28px;
    height: 28px;
  }
}