.wrapper {
  display: flex;
  grid-gap: 30px;
  align-items: center;
  transition: 0.2s;
  transform: translateY(0px);
}
.wrapper:hover {
  transform: translateX(5px);
  opacity: 0.7;
}
.showWrapper {
  opacity: 0;
}
.photo {
  position: relative;
  min-width: 70px;
  width: 70px;
  height: 70px;
  overflow: hidden;
  border-radius: 5px;
}
.photo img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}
.name {
  font-weight: 600;
}
.price {
  margin-left: auto;
  margin-right: 20px;
  white-space: nowrap;
}