.wrapper {
  position: relative;
  width: 100%;
  padding-right: 0px;
}
.block {
  position: relative;
  width: 100%;
  justify-self: center;
  border: none;
}
.iconBack {
  position: relative;
  width: 100%;
  opacity: 0;
}
.wrapperBlock {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.wrapperBlockSlide {
}
.wrapperBlockSlide img{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.wrapperItemsBlockSlide {
  position: relative;
  cursor: pointer;
  overflow: hidden;
  transition: opacity 0.3s;
  border-radius: 5px;
}
/*.wrapperItemsBlockSlide .swiper-slide-thumb-active{*/
/*  background: red;*/
/*}*/

.wrapperItemsBlockSlide img{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.wrapperLittle {
  position: relative;
  margin-top: 5px;
}
.iconBackLittle {
  position: relative;
  width: 100%;
  opacity: 0;
}
.wrapperLittleBlock {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media (max-width: 800px) {
  .wrapper {
    padding-right: 0px;
  }
}