.gold {
  background: var(--color);
  color: var(--backgroundColor);
  border: 2px solid var(--color);
  transition: 0.3s;
  opacity: 1;
  text-transform: uppercase;
  font-weight: 600;
}
.gold:disabled {
  opacity: 0.5;
}
.gold:hover {
  background: var(--backgroundColor);
  color: var(--color);
  opacity: 0.7
}
.gold:disabled:hover {
  background: var(--color);
  color: var(--backgroundColor);
  opacity: 0.5;
}