.wrapper {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.block {
  display: flex;
  flex-direction: column;
  padding: 30px 15px 20px;
  border: 1px solid #c4a370;
  width: 90%;
  max-width: 400px;
  border-radius: 5px;
  transform: translateY(-50px);
}
.formBlock {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
}
.formBlock input {
  background: none;
  border: 1px solid var(--color);
  padding: 15px;
}
.formBlock button{
  align-self: end;
  padding: 10px;
}
.backPage {
  position: absolute;
  top: 30px;
  left: 30px;
  display: flex;
  justify-content: center;
  grid-gap: 10px;
  font-weight: 600;
  font-size: 100%;
}
.backPage img{
  height: 20px;
  width: 30px;
}
.name {
  margin-left: 5px;
  font-size: 120%;
  font-weight: 500;
}
.logo {
  transform: translateY(-50px);
  width: 80px;
  margin-bottom: 30px;
}
@media (max-width: 800px) {
  .backPage {
    top: 30px;
    left: 5%;
  }
  .block button {
    padding: 15px;
    font-size: 110%;
  }
  .formBlock input {
    padding: 15px;
    font-size: 120%;
  }
}